

/* Mobile-first styles */
.container {
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: auto;
}

iframe {
  border: none;
  margin-bottom: 1.5rem;
  width: 100%;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.accordion-container {
  max-width: 400px;
  margin: 0 auto;
}

.accordion-summary {
  font-size: 1rem;
  font-weight: bold;
  padding: 0.5rem;
}

.accordion-details {
  font-size: 0.9rem;
  background: #f9f9f9;
  border-radius: 8px;
  margin-top: 0.5rem;
}

.typography {
  line-height: 1.5;
}

/* Breathing space for accordion items */
.accordion-item {
  border: 1px solid #ddd;
  border-radius: 8px;
  overflow: hidden;
  width: 100vw;
}

/* Medium and larger screens */
@media (min-width: 768px) {
  iframe {
    max-width: 640px;
    height: 360px;
  }

  .accordion-container {
    max-width: 600px;
  }

  .accordion-summary {
    font-size: 1.1rem;
  }

  .accordion-details {
    font-size: 1rem;
  }
  .accordion-item {
    border: 1px solid #ddd;
    border-radius: 8px;
    overflow: hidden;
    width: 100%;
  }
}
