.card-container {
    width: 100%;
    border-radius: 10px;
    text-align: center;
    overflow: hidden;
    background-color:#f1f1f1;
    text-align: center;
  }
  
  .card-header {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: 10px;
    color: black;
    font-weight:900;
    font-size: 12px;
  }
  
  .question-mark {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: rgb(233, 231, 231);
    color: rgb(175, 175, 175);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 12px;
    font-weight: bold;
    margin-left: 10px;
  }
  
  .card-content {
    padding: 10px 0;
    font-size: 20px;
    font-weight: bold;
    color: #000;
    font-family: 'Roboto', sans-serif;
    background-color: #f1f1f1;
  }
  