html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
  display: flex;
  flex-direction: column;
}

#root {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.main-content {
  width: "90%";
  justify-content: center;
  align-items: flex-start;
  overflow-y: auto;
  background-color: #f5f5f5;
}

header {
  height: 120px; /* Adjust to the height of ToolBar */
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 10;
}



.content-wrapper {
  margin-top: 120px; /* Matches the height of the header */
  margin-bottom: 60px; /* Matches the height of the footer */
  flex: 1;
  width: 100%;
  max-width: 1024px;
}
